.footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  margin-top: 15px;
  font-weight: 500;
}

.footer-link {
  text-decoration: none;
  color: #586069;
}

.footer-link:hover {
  color: #0366d6;
}

.footer-notes {
  display: flex;
  align-items: center;
  justify-content: center;
}

.notes-label {
  color: #586069;
}

.notes-wrapper {
  display: inline-flex;
  margin: 0 5px 0 5px;
  padding: 0;
}

.notes-item {
  padding: 0;
  border-radius: 2px;
  overflow: hidden;
  border: 1px solid rgba(27, 31, 35, 0.06);
  background: #ebedf0;
  height: 10px;
  width: 10px;
}

.notes-item.degree-1 {
  background: #9be9a8;
}
.notes-item.degree-2 {
  background: #40c463;
}
.notes-item.degree-3 {
  background: #30a14e;
}
.notes-item.degree-4 {
  background: #216e39;
}

.notes-item:not(:last-child) {
  margin-right: 5px;
}
